.map {
  .wrapper {
    position: relative;
    width: 2510px;
    height: 1608px;
  }

  .wrapper .map-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    border-right: 1px solid rgba(0, 0, 0, 0.4);
    overflow: hidden;
  }

  .wrapper .map {
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    transition: transform 2s;
  }

  .wrapper .new-map {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    // z-index: 2;
  }

  .wrapper .svg-wrapper .paths-wrapper line {
    stroke-width: 6;
    stroke: rgb(0, 0, 0);
  }

  .year-before {
    left: 5px;
  }
  .year-after {
    right: 5px;
  }
}
