.select-year {
  position: fixed;
  top: 5px;

  button {
    display: block;
    padding: 10px;
    font-size: 20px;
    font-weight: 700;
    color: black;
    font-family: Helvetica, Arial, sans-serif;
    background: rgba(white, 0.8);
    outline: none;
    cursor: pointer;

    &.active {
      background: rgba(rgb(145, 209, 99), 0.8);
    }
  }
}
